:root {
    --heights: 660px;
    --widths: 100%;
}
.slider-container {
    height: var(--heights);
    width: var(--widths);
    position: relative;
    margin: auto;
    overflow: hidden;
}

.active {
    display: inline-block;
}

.inactive {
    display: none;
}

.slides {
    height: var(--heights);
    width: var(--widths);
    position: relative;
}

.slide-image {
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
}
.slide-title,
.slide-text {
    width: 100%;
    height: 100%;
    color: white;
    font-size: 50px;
    position: absolute;
    text-align: center;
    top: 40%;
    z-index: 10;
    animation-duration: 2s;
    animation-name: bounceIn;
}
@keyframes bounceIn {
    0% {
        transform: scale(0.1);
        opacity: 0;
    }
    60% {
        transform: scale(1.2);
        opacity: 1;
    }
    100% {
        transform: scale(1);
    }
}
.slide-text {
    top: 65%;
    font-size: 2rem;
}
.prev,
.next {
    cursor: pointer;
    z-index: 100;
    position: absolute;

    top: 50%;
    width: auto;
    /* margin-top: 3rem; */
    padding: 1rem;
    font-size: 30px;
    font-weight: bold;
    border-radius: 0px 5px 5px 0px;
}

.prev:hover,
.next:hover {
    color: white;
    background-color: rgba(0, 0, 0, 0.6);
    transition: all 0.5s ease-in;
}
.next {
    right: 0;
    border-radius: 5px 0px 0px 5px;
}
.all-dots {
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    top: 85%;
    justify-content: center;
    z-index: 200;
}
.dot {
    cursor: pointer;
    height: 1rem;
    width: 1rem;
    margin: 0px 3px;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 50%;
    display: inline-block;
}
.active-dot,
.dot:hover {
    background-color: rgba(255, 255, 255, 0.5);
}
